import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Layout, Table, Modal, message, Alert } from 'antd';

import DisplayTree from '@/components/DisplayTree';
import OrgForm from './form';
import OrgToolBar from './toolBar'
import styles from './index.less';


const { Sider, Content } = Layout;
const { confirm } = Modal;

@inject('org')
@observer
export default class Org extends Component {
  constructor(props) {
    super(props);

    this.state = {
      // 
      modalVisible: false,
    }
  }

  componentWillMount() {
    const { org } = this.props;
    org.reset();
  }

  componentDidMount() {
    const { org } = this.props;
    org.fetchAuthMenuListButton();
    org.fetchAuthMenuFormButton();
    org.fetchTree();
    org.refreshList();

  }

  componentWillUnmount() {
    const { org } = this.props;
    org.reset();
  }

  // 
  onSelect = (selectedKeys) => {
    console.log('selected', selectedKeys);
    const { org } = this.props;

    if(selectedKeys.length > 0) {
      org.setData({
        selectedKeys: selectedKeys,
        selectedRowKeys: [],
      });
      
      org.fetchList({
        ParentID: selectedKeys[0],
        // CurrentPage: org.pagination.current,
        // PageSize: org.pagination.pageSize,
      });
    }
  }

  // /
  setModalVisible = (modalVisible) => {
    this.setState({
      modalVisible,
    });
  }

  // 
  handleNew = () => {
    this.setModalVisible(true);
  }

  // 
  handleEdit = () => {
    const { org } = this.props;

    org.fetchDetail().then(() => {
      org.setCurrentForm(org.currentDetail);
      this.setModalVisible(true);
    }).catch((e) => {
      message.error(`${e.Message}`);
    });
  }
  
  // 
  cleanSelectedKeys = (e) => {
    e.preventDefault();
    e.stopPropagation();
    
    const { org } = this.props;

    org.setData({
      selectedRowKeys: [],
    });
  }

  // 
  handleRemoveChecked = () => {

    const { org } = this.props;
    
    confirm({
      title: ` ${org.selectedRowKeys.length} ?`,
      content: '',
      onOk: () => {
        this.handleRemove(org.selectedRowKeys);
      },
    });
  }

  // 
  handleRemove = (Params) => {
    const { org } = this.props;

    org.remove({
      Params,
    }).then(() => {
      message.success('');
      // 
      org.setData({
        selectedRowKeys: org.selectedRowKeys.filter(item => (Params.indexOf(item) === -1)),
      });
    }).catch((e) => {
      message.error(`${e.Message}`);
    });
  }

  handleTableChange = (pagination, filters, sorter) => {
    console.log('pagination', pagination);
    console.log('filters', filters);
    console.log('sorter', sorter);
  }

  // 
  onSelectionChange = (selectedRowKeys, selectedRows) => {
    const { org } = this.props;

    org.setData({
      selectedRowKeys,
    });
  }

  render() {
    const { org } = this.props;
    const columns = [{ 
      title: '',
      dataIndex: 'NO',
      width: '7%',
      className:'alignCenter', 
      render: (text, row, index) =>(index + 1),
    }, {
      title: '',
      dataIndex: 'FullName',
      key: 'FullName',
      width: '40%',
    }, {
      title: '',
      dataIndex: 'SortCode',
      key: 'SortCode',
    }];

    return (
      <Layout className={styles.layout}>
        <Sider width={220} style={{ height: window.innerHeight - 110, overflowY: 'scroll', overflowX: 'auto', background: '#fff' }}>
          <DisplayTree
            treeList={[{
              id: '0',
              name: '',
              children: org.treeList.slice(),
            }]}
            defaultExpandedKeys={['0']}
            onSelect={this.onSelect}
            selectedKeys={org.selectedKeys.slice()}
          />
        </Sider>
        <Content style={{ paddingLeft: 24 }}>
          <OrgToolBar 
            org={org}
            handleNew={this.handleNew}
            handleEdit={this.handleEdit}
            handleRemoveChecked={this.handleRemoveChecked}
          />
          <OrgForm
            org={org}
            modalVisible={this.state.modalVisible}
            setModalVisible={this.setModalVisible}
          />
          <div className={styles.tableAlert}>
            <Alert
              message={(
                <div>
                   <a style={{ fontWeight: 600 }}>{org.selectedRowKeys.length}</a> 
                  <a onClick={this.cleanSelectedKeys} style={{ marginLeft: 24 }} disabled={org.selectedRowKeys.length <= 0}></a>
                </div>
              )}
              type="info"
              showIcon
            />
          </div>
          <Table 
            bordered
            size="small"
            loading={org.loading}
            pagination={false}
            dataSource={org.list.slice()}
            columns={columns}
            rowKey="UniqueID"
            onChange={this.handleTableChange}
            rowSelection={{
              selectedRowKeys: org.selectedRowKeys,
              onChange: this.onSelectionChange,
            }}
            scroll={{ y: window.innerHeight - 293 }}
          />
        </Content>
      </Layout>
    );
  }
}



